.blur-container .background {
  width: 26.875rem;
  height: 32.5rem;
  position: absolute;
  transform: translate(-50%, -50%);
  left: 50%;
  top: 50%;
}
.background .shape {
  height: 12.5rem;
  width: 12.5rem;
  position: absolute;
  border-radius: 50%;
}
.shape:first-child {
  background: linear-gradient(#1845ad, #23a2f6);
  left: -5rem;
  top: -5rem;
}
.shape:last-child {
  background: linear-gradient(to right, #ff512f, #f09819);
  right: -1.875rem;
  bottom: -5rem;
}
.blur-container > form {
  min-height: 32.5rem;
  min-width: 25rem;
  margin-top: 50px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  flex-grow: 1;
  padding-bottom: 1rem;
  background-color: rgba(255, 255, 255, 0.13);
  position: absolute;
  /* transform: translate(-50%, -50%); */
  /* top: 50%; */
  left: calc(50% - 13.5rem);
  border-radius: 0.625rem;
  backdrop-filter: blur(0.625rem);
  border: 0.125rem solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 0 2.5rem rgba(155, 150, 195, 0.6);
  padding: 3.125rem 2.1875rem;
}
.blur-container > form * {
  font-family: "Poppins", sans-serif;

  letter-spacing: 0.0313rem;
  outline: none;
  border: none;
}
.blur-container form h1 {
  color: #ffffff;
  font-size: 1.8rem;
  font-weight: 500;
  line-height: 2rem;
  text-align: center;
}
.blur-container form p {
  color: #ffffff;
  font-size: 1rem;
  font-weight: 300;
  line-height: 1.25rem;
  text-align: center;
}

.blur-container label {
  color: #ffffff;
  display: block;
  margin-top: 1.875rem;
  font-size: 1rem;
  font-weight: 500;
}
.blur-container input {
  color: #ffffff;
  display: block;
  height: 2.7rem;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.07);
  border-radius: 0.1875rem;
  padding: 0 0.625rem;
  margin-top: 0.5rem;
  font-size: 1.0625rem;
  font-weight: 500;
}

.blur-container button {
  margin-top: 3.125rem;
  width: 100%;
  background-color: #ffffff;
  color: #080710;
  padding: 0.9375rem 0;
  font-size: 1.125rem;
  font-weight: 600;
  border-radius: 0.3125rem;
  cursor: pointer;
}

.check {
  margin-left: 0.9375rem;
  color: greenyellow;
}
.not-check {
  margin-left: 0.9375rem;
  color: red;
}

.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

.switch input { 
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked + .slider {
  background-color: #2196F3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}



.burger-menu {
	display: none;
	cursor: pointer;
	font-weight: 100;
	z-index: 5;
  }
  
  .burger-bar {
	width: 2em;
	height: 0.1em;
	background-color: rgb(255, 0, 0);
	border-radius: 0.5em;
	margin-top: 8px;
	z-index: 99;
	
  }
  
  .menu {
	width: 45%;
	height: 20vh;
	background-color: rgb(0, 0, 0);
	position: absolute;
	top: 40px;
	padding: 30px;
	font-weight: 700;
	color: #ffffff;
	/* border: #00f5a0 2px solid ; */
	border-radius:20px ;
	z-index: 1;
  } 
  .menu >div {
	display: flex;
	flex-direction: column;
	justify-content: space-around;
  }
  .menu >div *{
	padding-left: 8px ;
	border-left:4px solid #8f1919 ;
  }
  
  /* ------------- sliding menu ------------------ */
  .hidden {
	display: none ;
  }
  
  .visible {
	display: inherit;
  }
  
  /* ----- animations--------- */
  /* clicked */
  .burger-bar.clicked:nth-child(1){
	transform: rotate(-45deg) translate(-15px);
	transition: ease-out 0.5s;
  }
  
  .burger-bar.clicked:nth-child(2){
	transform: scale(0.1);
	transition: ease-out 0.5s;
  }
  
  .burger-bar.clicked:nth-child(3){
	transform: rotate(45deg) translate(-15px);
	transition: ease-out 0.5s;
  }
  
  /* unclicked */
  .burger-bar.unclicked {
	transform: rotate(0) translate(0);
	transition: cubic-bezier(0.175, 0.885, 0.32, 1.275) 0.5s;
  }
  

